*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
  background-color: #e7e5e5;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page__body {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  background-color: #ffffff;
  color: #000000;
}
@media screen and (min-width: 1200px) {
  .page__body {
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }
}

.main-header {
  position: relative;
  width: 100%;
  display: flex;
}

.main-header__logo-image {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

.main-nav {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  min-height: 56px;
}
@media screen and (min-width: 768px) {
  .main-nav {
    display: flex;
    background-color: #ffffff;
  }
}

.main-nav__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
  display: block;
  min-width: 23px;
  min-height: 22px;
  cursor: pointer;
  border: none;
  z-index: 2;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
@media screen and (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 23px;
  height: 22px;
  background: #ffffff url("../img/nav-menu-icon.svg") no-repeat;
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__wrapper {
    display: none;
  }
}
.main-nav--opened .main-nav__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
  min-width: 23px;
  min-height: 22px;
  cursor: pointer;
  border: none;
  background-color: transparent;
}
.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 23px;
  height: 22px;
  background: url("../img/cross-icon.svg") no-repeat;
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--nojs .main-nav__wrapper {
  min-height: 0;
  display: block;
}

.main-nav--nojs .main-nav__list {
  display: block;
}
@media screen and (min-width: 768px) {
  .main-nav--nojs .main-nav__list {
    display: flex;
  }
}
@media screen and (min-width: 1200px) {
  .main-nav--nojs .main-nav__list {
    display: flex;
  }
}

.main-nav--nojs .main-nav__link--current {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 55px;
  text-decoration: none;
  font-family: inherit;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  border: none;
}
@media screen and (min-width: 768px) {
